import React from "react"
import PropTypes from "prop-types"
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"
import classNames from "classnames"

const propTypes = {
  isAlign: PropTypes.oneOf(["left", "right", null]),
  isSize: PropTypes.oneOf(["large", "medium", "small", null]),
  faProps: PropTypes.shape(FontAwesomeIcon.propTypes),
}

const defaultProps = {
  isAlign: null,
  isSize: null,
  faProps: FontAwesomeIcon.defaultProps,
}

const Icon = ({ isAlign, isSize, faProps, ...htmlProps }) => {
  const className = classNames({
    icon: true,
    [`is-${isAlign}`]: !!isAlign,
    [`is-${isSize}`]: !!isSize,
  })

  return (
    <span {...htmlProps} className={className}>
      <FontAwesomeIcon {...faProps} />
    </span>
  )
}

Icon.propTypes = propTypes
Icon.defaultProps = defaultProps

export default Icon
